body {
  font-family: sans-serif; }

.modal-open {
  overflow: hidden !important; }

.showModal {
  z-index: 1000;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  margin: auto;
  position: relative;
  opacity: 0; }

.hideModal {
  z-index: 1000;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  margin: auto;
  position: relative;
  opacity: 1; }

.modal {
  z-index: 1000;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  margin: auto;
  position: relative;
  opacity: 1; }

.animateUp {
  transform: translateY(0%);
  visibility: visible;
  -webkit-animation-name: modalSlideUp;
  -webkit-animation-duration: 1s;
  animation-name: modalSlideUp;
  animation-duration: 1s;
  /*  animation-delay: 0.5s;*/
  opacity: 1; }

/* Safari 4.0 - 8.0 */
@-webkit-keyframes modalSlideUp {
  0% {
    opacity: 0;
    visibility: visible;
    transform: translateY(50%); }
  75% {
    transform: translateY(-10px); }
  100% {
    opacity: 1;
    visibility: visible;
    transform: translateY(0%); } }

/* Standard syntax */
@keyframes modalSlideUp {
  0% {
    opacity: 0;
    visibility: visible;
    transform: translateY(50%); }
  75% {
    transform: translateY(-10px); }
  100% {
    opacity: 1;
    visibility: visible;
    transform: translateY(0%); } }

.animateDown {
  transform: translateY(0%);
  visibility: visible;
  -webkit-animation-name: modalSlideDown;
  -webkit-animation-duration: 1s;
  animation-name: modalSlideDown;
  animation-duration: 1s;
  /*  animation-delay: 0.5s;*/
  opacity: 0; }

/* Safari 4.0 - 8.0 */
@-webkit-keyframes modalSlideDown {
  0% {
    opacity: 1;
    visibility: visible;
    transform: translateY(0%); }
  /* 75% {transform: translateY(10px)} */
  100% {
    opacity: 0;
    transform: translateY(50%);
    visibility: hidden; } }

/* Standard syntax */
@keyframes modalSlideDown {
  0% {
    opacity: 1;
    visibility: visible;
    transform: translateY(0%); }
  /*  75% {transform: translateY(10px)}*/
  100% {
    opacity: 0;
    transform: translateY(50%);
    visibility: hidden; } }

.modal_container {
  width: 100%;
