  animation-duration: 0.1s;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
  height: 100%;
  -webkit-box-pack: stretch;
  -ms-flex-pack: stretch;
  justify-content: stretch;
  justify-items: stretch;
  -webkit-box-align: stretch;
  -ms-flex-align: stretch;
  align-items: stretch; }

.body-container {
  z-index: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  -ms-flex-direction: row;
  flex-direction: row; }

.modal-wrapper {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%; }

/* Safari 4.0 - 8.0 */
@-webkit-keyframes fadeIn {
  0% {
    opacity: 0; }
  50% {
    opacity: 0.4; }
  75% {
    opacity: 0.8; }
  100% {
    opacity: 1; } }

/* Standard syntax */
@keyframes fadeIn {
  0% {
    opacity: 0; }
  50% {
    opacity: 0.4; }
  75% {
    opacity: 0.8; }
  100% {
    opacity: 1; } }

.modal-body {
  margin: 0 auto;
