.modal-background-color {
  /* modal background fixed across whole screen */
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  /* semi-transparent black  */
  background-color: rgba(0, 0, 0, 0.6);
  /* opacity: 0.55; */
  /* z-index must be below .modal and above everything else  */
  z-index: 900;
  -webkit-animation-name: bgFadeIn;
  /* Safari 4.0 - 8.0 */
  -webkit-animation-duration: 0.3s;
  /* Safari 4.0 - 8.0 */
  animation-name: bgFadeIn;
  animation-duration: 0.3s;
  /*filter:blur(3px);*/
  -webkit-transform: scale(1.1);
  transform: scale(1.1); }

.gosell-modal-blur-bg {
  filter: blur(8px);
  -webkit-filter: blur(8px); }

/* Safari 4.0 - 8.0 */
@-webkit-keyframes bgFadeIn {
  0% {
    opacity: 0.1; }
  100% {
    opacity: 0.75;
    -webkit-transform: scale(1.1);
    transform: scale(1.1);
    filter: blur(2px); } }

/* Standard syntax */
@keyframes bgFadeIn {
  0% {
    opacity: 0.1; }
  100% {
    opacity: 0.75;
    -webkit-transform: scale(1.1);
    transform: scale(1.1);
    filter: blur(2px); } }

.page {
  /* modal background fixed across whole screen */
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: white;
