  background-repeat: no-repeat;
  background-size: 140px; }

.page {
  background-image: url(data:image/png;base64,);
  background-position: 90% 90%;
  background-repeat: no-repeat;
  background-size: 140px; }

.body-container {
  height: 100%; }

.tap-wait {
  height: 100%;
  justify-content: center;
  align-items: center; }

