.tap-confirm {
  height: 100%;
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.5s ease-in; }

.tap-confirm-fadeIn {
  height: 0px;
  opacity: 1;
  visibility: visible; }

.tap-confirm-back {
  width: 100%;
  height: 65px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer; }
